@import url('https://fonts.googleapis.com/css2?family=Montserrat&family=Varela&display=swap');

* {
    box-sizing: border-box;
    user-select: none;
}

:root {
    --mainColor: #d70a84;
    --gap: 25px;
}

html {
    font-size: 18px;
    line-height: 26px;
    color: var(--mainColor);
    font-family: 'Montserrat', sans-serif;
}

body {
    background-color: #131417;
    margin: 0;
}

main {
    flex-direction: column;
    padding: var(--gap);
}

h1,
h2,
h3 {
    font-family: 'Varela', sans-serif;
    margin: 0 0 0.5em;
}

h1 {
    font-size: 1.75rem;
}

h2 {
    font-size: 1.5rem;
}

h3 {
    font-size: 1.25rem;
}

.flex-center {
    display: flex;
    justify-content: center;
    align-items: center;
}

body {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 200px calc(100vh - 350px) 150px;
    grid-auto-rows: 1fr;
    gap: 0px 0px;
    grid-auto-flow: row;
    grid-template-areas:
        "header"
        "page_container"
        "footer";
}

header {
    grid-area: header;
}

.page_container {
    display: grid;
    grid-template-columns: 300px 1fr;
    grid-template-rows: 100%;
    gap: 0px 0px;
    grid-auto-flow: row;
    grid-template-areas:
        "aside content";
    grid-area: page_container;
}

aside {
    grid-area: aside;
    width: 100%;
    height: 100%;
}

main {
    grid-area: content;
    width: 100%;
    height: 100%;
}

footer {
    grid-area: footer;
    height: 100%;
}


.member_list {display: flex;gap: var(--gap);flex-wrap: wrap;}